<template>
  <view class="admin-container">
    <view class="page-header">
      <text class="page-title">ÂêàÂùõÊ≥ï‰ºöÂ§¥ÂõæÁÆ°ÁêÜ</text>
      <text class="page-desc">ÁÆ°ÁêÜÂêàÂùõÊ≥ï‰ºöÈ°µÈù¢Â§¥ÂõæÔºà‰ªÖÊîØÊåÅ‰∏ÄÂº†ÂõæÁâáÔºâ</text>
    </view>
    
    <!-- ÂçïÂº†Â§¥ÂõæÁÆ°ÁêÜ -->
    <view v-if="currentBanner.image && !isEdit" class="banner-card">
      <view class="banner-preview">
        <image :src="currentBanner.image" class="banner-img" mode="aspectFill" />
        <view class="banner-overlay">
          <view class="banner-actions">
            <button class="action-btn edit" @click="isEdit = true">ÁºñËæë</button>
            <button v-if="currentBanner._id && !isEdit" class="action-btn delete" @click="deleteBanner">Âà†Èô§</button>
          </view>
        </view>
      </view>
      <view class="banner-info">
        <view class="info-row">
          <text class="info-label">Áä∂ÊÄÅÔºö</text>
          <text class="info-value" :class="currentBanner.enabled ? 'enabled' : 'disabled'">
            {{ currentBanner.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}
          </text>
        </view>
        <view class="info-row">
          <text class="info-label">Êõ¥Êñ∞Êó∂Èó¥Ôºö</text>
          <text class="info-value">{{ formatTime(currentBanner.update_time || currentBanner.create_time) }}</text>
        </view>
      </view>
    </view>
    <!-- Ê∑ªÂä†/ÁºñËæëË°®ÂçïÁõ¥Êé•Âú®ÂΩìÂâçÈ°µÈù¢ÊòæÁ§∫ -->
    <view v-if="isEdit" class="banner-edit-form">
      <view class="form-content">
        <view class="form-item">
          <text class="form-label">Â§¥ÂõæÂõæÁâá *</text>
          <view class="image-upload" @click="chooseImage">
            <image v-if="currentBanner.image" :src="currentBanner.image" class="upload-preview" mode="aspectFill" />
            <view v-else class="upload-placeholder">
              <text class="upload-icon">üì∑</text>
              <text class="upload-text">ÁÇπÂáªÈÄâÊã©ÂõæÁâá</text>
            </view>
          </view>
        </view>
        <view class="form-item">
          <text class="form-label">Áä∂ÊÄÅ</text>
          <switch :checked="currentBanner.enabled" @change="onSwitchChange" color="#667eea" />
          <text class="switch-label">{{ currentBanner.enabled ? 'ÂêØÁî®' : 'Á¶ÅÁî®' }}</text>
        </view>
      </view>
      <view class="form-actions">
        <button class="cancel-btn" type="button" @click="cancelEdit">ÂèñÊ∂à</button>
        <button class="save-btn" type="button" @click="saveBanner">‰øùÂ≠ò</button>
      </view>
    </view>
    <view v-else-if="!currentBanner._id && !isEdit" class="no-banner">
      <text class="no-banner-text">ÊöÇÊó†Â§¥ÂõæÔºåËØ∑Ê∑ªÂä†</text>
      <button class="add-btn" type="button" @click="showAddForm">+ Ê∑ªÂä†Â§¥Âõæ</button>
    </view>
  </view>
</template>

<script>
// ÂØºÂÖ•‰∫ëÂØπË±°
const jointManagement = uniCloud.importObject('joint-management')

export default {
  
  data() {
    return {
      isEdit: false,
      currentBanner: {
        _id: '',
        image: '',
        enabled: true,
        create_time: '',
        update_time: ''
      }
    }
  },
  
  onLoad() {
    console.log('[ÂêéÂè∞] È°µÈù¢ onLoad')
    this.loadBanners()
  },
  
  onShow() {
    console.log('[ÂêéÂè∞] È°µÈù¢ onShow')
    // ÊØèÊ¨°È°µÈù¢ÊòæÁ§∫Êó∂ÈáçÊñ∞Âä†ËΩΩÊï∞ÊçÆÔºåÁ°Æ‰øùÊï∞ÊçÆÊúÄÊñ∞
    this.loadBanners()
  },
  

  
  methods: {
    async loadBanners() {
      try {
        console.log('[ÂêéÂè∞] ÂºÄÂßãÂä†ËΩΩÂ§¥Âõæ...')
        const result = await jointManagement.getBanners()
        console.log('[ÂêéÂè∞] Ëé∑ÂèñÂ§¥ÂõæÁªìÊûú:', result)
        // ËøáÊª§Êéâ_id‰∏∫Á©∫ÁöÑÊï∞ÊçÆ
        const validBanners = result.success && result.data ? result.data.filter(b => b._id) : []
        if (validBanners.length > 0) {
          let banner = validBanners[0]
          // ‰øùÂ≠òÂéüÂßãÁöÑfileIDÁî®‰∫éÁºñËæë
          const originalImage = banner.image
          if (banner.image && !/^https?:\/\//.test(banner.image)) {
            // fileIDËΩ¨‰∏¥Êó∂URLÁî®‰∫éÊòæÁ§∫
            const tempRes = await uniCloud.getTempFileURL({ fileList: [banner.image] })
            banner.image = tempRes.fileList[0].tempFileURL
            // Âä†Êó∂Èó¥Êà≥Âº∫Âà∂Âà∑Êñ∞ÂõæÁâáÁºìÂ≠ò
            banner.image = banner.image + '?t=' + Date.now()
            console.log('[ÂêéÂè∞] fileIDËΩ¨‰∏¥Êó∂URL:', banner.image)
          }
          // ‰øùÂ≠òÂéüÂßãfileIDÔºåÁî®‰∫éÁºñËæëÊó∂‰øùÂ≠ò
          banner.originalImage = originalImage
          this.currentBanner = banner
          console.log('[ÂêéÂè∞] loadBanners ËµãÂÄºÂêé currentBanner:', JSON.stringify(this.currentBanner))
        } else {
          this.currentBanner = {
            image: '',
            enabled: true,
            create_time: '',
            update_time: ''
          }
          console.log('[ÂêéÂè∞] Ê≤°ÊúâÊâæÂà∞Â§¥ÂõæÔºåcurrentBanner:', JSON.stringify(this.currentBanner))
        }
      } catch (error) {
        console.error('[ÂêéÂè∞] Âä†ËΩΩÂêàÂùõÊ≥ï‰ºöÂ§¥ÂõæÂ§±Ë¥•:', error)
        uni.showToast({ title: 'Âä†ËΩΩÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    showAddForm() {
      console.log('[ÂêéÂè∞] ÁÇπÂáªÊ∑ªÂä†Â§¥Âõæ')
      this.isEdit = true
      this.currentBanner = {
        image: '',
        enabled: true,
        create_time: '',
        update_time: ''
      }
    },
    
    cancelEdit() {
      console.log('[ÂêéÂè∞] ÂèñÊ∂àÁºñËæë')
      this.isEdit = false
      this.loadBanners()
    },
    
    chooseImage() {
      console.log('[ÂêéÂè∞] ÁÇπÂáªÈÄâÊã©ÂõæÁâá')
      uni.chooseImage({
        count: 1,
        sizeType: ['compressed'],
        sourceType: ['album', 'camera'],
        success: async (res) => {
          try {
            uni.showLoading({ title: '‰∏ä‰º†‰∏≠...' })
            const uploadRes = await uniCloud.uploadFile({
              filePath: res.tempFilePaths[0],
              cloudPath: 'joint_banners/' + Date.now() + '.jpg'
            })
            uni.hideLoading()
            this.currentBanner.image = uploadRes.fileID
            // ÂêåÊó∂Êõ¥Êñ∞originalImageÔºåÁ°Æ‰øù‰øùÂ≠òÊó∂‰ΩøÁî®Êñ∞ÁöÑfileID
            this.currentBanner.originalImage = uploadRes.fileID
            console.log('[ÂêéÂè∞] ÂõæÁâá‰∏ä‰º†ÊàêÂäüÔºåfileID:', uploadRes.fileID)
            uni.showToast({ title: 'ÂõæÁâá‰∏ä‰º†ÊàêÂäü' })
            // ‰∏ä‰º†ÊàêÂäüÂêéËá™Âä®‰øùÂ≠ò
            await this.saveBanner()
          } catch (err) {
            uni.hideLoading()
            console.log('[ÂêéÂè∞] ÂõæÁâá‰∏ä‰º†Â§±Ë¥•:', err)
            uni.showToast({ title: 'ÂõæÁâá‰∏ä‰º†Â§±Ë¥•', icon: 'none' })
          }
        }
      })
    },
    
    onSwitchChange(e) {
      this.currentBanner.enabled = e.detail.value
    },
    
    async saveBanner() {
      if (!this.currentBanner.image) {
        uni.showToast({ title: 'ËØ∑ÈÄâÊã©Â§¥Âõæ', icon: 'none' })
        return
      }
      
      try {
        console.log('[ÂêéÂè∞] ‰øùÂ≠òÂ§¥ÂõæÔºåcurrentBanner:', JSON.stringify(this.currentBanner))
        
        // Á°ÆÂÆöË¶Å‰øùÂ≠òÁöÑÂõæÁâáURLÔºö‰ºòÂÖà‰ΩøÁî®originalImageÔºàfileIDÔºâÔºåÂõ†‰∏∫imageÂèØËÉΩÊòØ‰∏¥Êó∂URL
        let imageToSave = this.currentBanner.originalImage || this.currentBanner.image
        
        // Â¶ÇÊûúoriginalImageÊòØ‰∏¥Êó∂URLÔºåÂàô‰ΩøÁî®imageÔºàfileIDÔºâ
        if (imageToSave && imageToSave.startsWith('http') && imageToSave.includes('cdn.bspapp.com')) {
          // Â¶ÇÊûúoriginalImageÊòØ‰∏¥Êó∂URLÔºåÂ∞ùËØï‰ΩøÁî®image
          if (this.currentBanner.image && !this.currentBanner.image.startsWith('http')) {
            imageToSave = this.currentBanner.image
          }
        }
        
        console.log('[ÂêéÂè∞] ‰øùÂ≠òÂâçÊ£ÄÊü•:')
        console.log('[ÂêéÂè∞] - currentBanner.image:', this.currentBanner.image)
        console.log('[ÂêéÂè∞] - currentBanner.originalImage:', this.currentBanner.originalImage)
        console.log('[ÂêéÂè∞] - ÊúÄÁªà‰øùÂ≠òÁöÑimageToSave:', imageToSave)
        
        const data = {
          image: imageToSave,
          enabled: this.currentBanner.enabled
        }
        
        console.log('[ÂêéÂè∞] ÂÆûÈôÖ‰øùÂ≠òÁöÑÂõæÁâáÊï∞ÊçÆ:', data)
        
        if (this.currentBanner._id) {
          // Êõ¥Êñ∞Áé∞ÊúâÂ§¥Âõæ
          console.log('[ÂêéÂè∞] Êõ¥Êñ∞Â§¥Âõæ')
          const result = await jointManagement.updateBanner({ 
            _id: this.currentBanner._id, 
            ...data 
          })
          if (!result.success) {
            throw new Error(result.message || 'Êõ¥Êñ∞Â§±Ë¥•')
          }
        } else {
          // Ê∑ªÂä†Êñ∞Â§¥Âõæ
          console.log('[ÂêéÂè∞] Êñ∞Â¢ûÂ§¥Âõæ')
          const result = await jointManagement.addBanner({ 
            image: data.image,
            enabled: data.enabled
          })
          if (!result.success) {
            throw new Error(result.message || 'Ê∑ªÂä†Â§±Ë¥•')
          }
        }
        
        this.isEdit = false
        setTimeout(() => {
          this.loadBanners()
        }, 500)
        uni.showToast({ title: '‰øùÂ≠òÊàêÂäü' })
      } catch (error) {
        console.error('[ÂêéÂè∞] ‰øùÂ≠òÂêàÂùõÊ≥ï‰ºöÂ§¥ÂõæÂ§±Ë¥•:', error)
        uni.showToast({ title: '‰øùÂ≠òÂ§±Ë¥•', icon: 'none' })
      }
    },
    
    async deleteBanner() {
      console.log('[ÂêéÂè∞] deleteBanner Ë∞ÉÁî®ÔºåcurrentBanner:', JSON.stringify(this.currentBanner))
      // Èò≤Âæ°ÊÄßÊ£ÄÊü•
      if (!this.currentBanner || !this.currentBanner._id) {
        uni.showToast({ title: 'Â§¥ÂõæÊï∞ÊçÆÊó†Êïà', icon: 'none' })
        return
      }
      console.log('[ÂêéÂè∞] ÂºÄÂßãÂà†Èô§Â§¥ÂõæÔºåID:', this.currentBanner._id)
      console.log('[ÂêéÂè∞] ÂΩìÂâçÂ§¥ÂõæÊï∞ÊçÆ:', this.currentBanner)
      
      // È™åËØÅIDÂèÇÊï∞
      if (!this.currentBanner._id) {
        console.error('Âà†Èô§Â§±Ë¥•ÔºöÂ§¥ÂõæID‰∏∫Á©∫')
        uni.showToast({ title: 'Â§¥ÂõæÊï∞ÊçÆÊó†Êïà', icon: 'none' })
        return
      }
      
      uni.showModal({
        title: 'Á°ÆËÆ§Âà†Èô§',
        content: 'Á°ÆÂÆöË¶ÅÂà†Èô§Ëøô‰∏™Â§¥ÂõæÂêóÔºü',
        success: async (res) => {
          if (res.confirm) {
            try {
              console.log('[ÂêéÂè∞] Áî®Êà∑Á°ÆËÆ§Âà†Èô§ÔºåË∞ÉÁî®‰∫ëÂáΩÊï∞...')
              console.log('‰º†ÈÄíÁöÑÂèÇÊï∞:', { bannerId: this.currentBanner._id })
              
              const result = await jointManagement.deleteBanner({ _id: this.currentBanner._id })
              console.log('[ÂêéÂè∞] Âà†Èô§‰∫ëÂáΩÊï∞ËøîÂõûÁªìÊûú:', result)
              
              if (result.success) {
                await this.loadBanners()
                uni.showToast({ title: 'Âà†Èô§ÊàêÂäü', icon: 'success' })
              } else {
                const errorMsg = result.message || 'Âà†Èô§Â§±Ë¥•'
                console.error('[ÂêéÂè∞] Âà†Èô§Â§±Ë¥•ËØ¶ÊÉÖ:', result)
                throw new Error(errorMsg)
              }
            } catch (error) {
              console.error('[ÂêéÂè∞] Âà†Èô§ÂêàÂùõÊ≥ï‰ºöÂ§¥ÂõæÂ§±Ë¥•:', error)
              uni.showToast({ 
                title: error.message || 'Âà†Èô§Â§±Ë¥•', 
                icon: 'none' 
              })
            }
          }
        }
      })
    },
    
    formatTime(time) {
      if (!time) return ''
      const date = new Date(time)
      return `${date.getFullYear()}-${String(date.getMonth() + 1).padStart(2, '0')}-${String(date.getDate()).padStart(2, '0')}`
    }
  }
}
</script>

